@import url("https://fonts.googleapis.com/css?family=Bubblegum+Sans|Gochi+Hand");
body {
  background-image: url('http://cdn.backgroundhost.com/backgrounds/subtlepatterns/wood_1.png');
}
.navbar {
  border-radius: 0;
}
.bookshelf {
  font-family: 'Gochi Hand', cursive;
  background-color: #4f603e;
  font-size: 1.3em;
  height: 80vh;
}
.bookshelf h2 {
  color: white;
}
.bookshelf li {
  cursor: pointer;
}
.bookdetail {
  font-family: 'Bubblegum Sans', cursive;
  padding: 25px;
  height: 80vh;
}
.bookdetail, .jumbotron {
  background: tan;
}
.col-sm-6, .col-sm-12 {
  margin-top: 20px;
  font-size: 1.3em;
}



